.Container {
  width: 100vw;
  height: vh;
  background-color: #ffcd9e;
  display: block;
  font-family: "Inter";
}
.Heading {
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  color: #282828;
  width: 80%;
  margin-left: 10vw;
  margin-top: 8vh;
  margin-bottom: 6vh;
}

.Content {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  color: #484848;
  width: 65%;
  line-height: 160%;
  margin-left: 10vw;
  margin-bottom: 8vh;
}

.text {
  margin-left: 10vw;
  margin-bottom:4vh ;
}

.emailBox {
  margin-left: 10vw;
  margin-top: 2.5vh;
}

.lowerText {
  margin-left: 12vw;
  color: aliceblue;
  font-size: 14px;
}

input {
  height: 32px;
  background: #ffffff;
  border-radius: 8px;
  padding-left: 20px;
  width: 15rem;
  margin-top: 12px;
  margin-right: 12px;
  border: 3px solid #000000;
}
 button{
  background: #282828;
  border: none;
  border-radius: 8px;
  color: white;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  width: 10em;
  height: 40px;
  cursor: pointer;
 }